export default {
	name: "sunui-star",
	props: {
		type: {
			type: String,
			default: 'star'
		},
		isTips:{
			type:Boolean,
			default:false
		},
		starSize: {
			type: String,
			default: '1.5em'
		},
		index:{
			type: Number,
			default: 0
		},
		currentIndex:{
			type: Number,
			default: 0
		},
		maxStar: {
			type: Number,
			default: 5
		},
		defaultStar: {
			type: Number,
			default: 5
		},
		disabledStar: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			curStarNum: 0,
			icon: 'icon-aixin'
		}
	},
	created() {
		this.curStarNum = this.defaultStar;
	},
	methods: {
		changeStar(e) {
			if (this.disabledStar) {
				return;
			}
			this.curStarNum = Number(e.currentTarget.dataset.value) + 1;
			this.$emit("changeStar", {
				curStar: this.curStarNum,
				index:   this.index,
				currentIndex : this.currentIndex,
			});
			if(this.isTips){
				uni.showToast({
					title:`${this.curStarNum}颗`,
					icon:'none'
				})
			}
		}
	}
}
